/* eslint-disable no-unused-vars */
import React, { useState } from 'react'
import axios from 'axios'
import {
	Grid,
	Button,
	Form,
	InputNumber,
	Layout,
	Row,
	Col,
	Select,
	Switch,
	Spin, Typography, PageHeader
} from 'antd'
import { useThemeSwitcher } from 'react-css-theme-switcher'
import './App.css'


const { Header, Footer, Content } = Layout
const { Option } = Select
const { Title } = Typography
const { useBreakpoint } = Grid

const App = () => {
	const screens = useBreakpoint()
	const [consulting, setConsulting] = useState(null)
	const [status, setStatus] = useState(false)
	const [result, setResult] = useState(null)
	const [isDarkMode, setIsDarkMode] = React.useState()
	const { switcher, currentTheme, statusTheme, themes } = useThemeSwitcher()
	const [form] = Form.useForm()

	const toggleTheme = (isChecked) => {
		setIsDarkMode(isChecked)
		switcher({ theme: isChecked ? themes.dark : themes.light })
	}

	// Avoid theme change flicker
	if (statusTheme === 'loading') {
		return null
	}

	const formItemLayout = {
		// labelCol: {
		// 	xs: {span: 24},
		// 	sm: {span: 24},
		// },
		// wrapperCol: {
		// 	xs: {span: 24},
		// 	sm: {span: 24},
		// },
	}

	const onFinish = async (values: any) => {
		console.log('Received values of form: ', values, screens)
		setConsulting(true)
		window.scrollTo(0, 0)
		let send = {
			age: values.age,
			height: values.height,
			weight: values.weight,
			fcvc: values.fcvc,
			ncp: values.ncp,
			ch2o: values.ch2o,
			faf: values.faf,
			tue: values.tue,
			gender: Number.parseInt(values.gender),
			fhwo: values.fhwo ? 1 : 0,
			favc: values.favc ? 1 : 0,
			caec: Number.parseInt(values.caec),
			smoke: values.smoke ? 1 : 0,
			scc: values.scc ? 1 : 0,
			calc: Number.parseInt(values.caec),
			mtrans: Number.parseInt(values.mtrans)
		}

		let responses = {
			'Insufficient_Weight': {
				name: 'Peso insuficiente',
				image: require('./assets/images/bmi1.png').default,
				color: null
			},
			'Normal_Weight': {
				name: 'Peso normal',
				image: require('./assets/images/bmi2.png').default,
				color: null
			}, 'Overweight_Level_I': {
				name: 'Sobrepeso nivel I',
				image: require('./assets/images/bmi3.png').default,
				color: null
			}, 'Overweight_Level_II': {
				name: 'Sobrepeso nivel II',
				image: require('./assets/images/bmi4.png').default,
				color: null
			}, 'Obesity_Type_I': {
				name: 'Obesidad tipo I',
				image: require('./assets/images/bmi5.png').default,
				color: null
			}, 'Obesity_Type_II': {
				name: 'Obesidad tipo II',
				image: require('./assets/images/bmi6.png').default,
				color: null
			}, 'Obesity_Type_III': {
				name: 'Obesidad tipo III',
				image: require('./assets/images/bmi7.png').default,
				color: null
			}
		}

		console.log('data: ', send)

		await axios
			.post('https://protected-earth-68512.herokuapp.com/api/predict', send)
			.then(async (response) => {
				console.log('response: ', response)
				if (response.data?.data?.data) {
					const data = JSON.parse(response.data.data.data)
					console.log('response: ', data)
					if (data.data[0].output && data.data[0].output[0] in responses) {
						setStatus(data.success)
						setResult(responses[data.data[0].output[0]])
					} else {
						setStatus(false)
					}

				}
				setConsulting(false)
			})
			.catch((err) => {
				setConsulting(false)
				setStatus(false)
				console.log('error ', err)
			})
	}

	const tailFormItemLayout = {
		wrapperCol: {
			xs: {
				span: 24,
				offset: 0,
			},
			sm: {
				span: 16,
				offset: 8,
			},
		},
	}

	return (
		<Layout>
			<Spin spinning={consulting} size="large">
				<Header style={{ height: 'auto', padding:'0 0' }}>
					<PageHeader    ghost={false}
						title={<Title level={4}>Clasificación de Peso</Title>}
						extra={[
							<Switch key="1" checked={isDarkMode} onChange={toggleTheme} 
								checkedChildren={<img src={require('./assets/images/moon.png').default} alt='moon' style={{ height:'1.5em' }} />}
								unCheckedChildren={<img src={require('./assets/images/sunny.png').default} alt='sunny' style={{ height:'1.5em' }} />} />
						]}
					>
					</PageHeader>

				</Header>
				<Content style={{ padding: '2em 5em' }}>
					<Layout>
						<Row justify='center'>
							<Col md={12} sm={24} order={screens.xs ? 2 : 1}>
								<Title level={1} style={{ textAlign: 'center' }}>Formulario</Title>
								<Form
									{...formItemLayout}
									layout="vertical"
									form={form}
									name="register"
									onFinish={onFinish}
									initialValues={{
										fhwo: false, smoke: false, favc: false, scc: false
									}}
									scrollToFirstError>
									<Form.Item
										name="age"
										label="Edad"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 99,
												message: 'Por favor, ingrese un número entre 0-99'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="height"
										label="Estatura (m)"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 2.51,
												message: 'Por favor, ingrese un número entre 0-2.51'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="weight"
										label="Peso (kg)"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 595,
												message: 'Por favor, ingrese un número entre 0-595'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="gender"
										label="Género"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione una opción'
											}
										]}>
										<Select placeholder="Selecciona tu género">
											<Option value="1">Masculino</Option>
											<Option value="0">Femenimo</Option>
										</Select>
									</Form.Item>
									<Form.Item
										name="ncp"
										label="# de comidas principales al día"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 5,
												message: 'Por favor, ingrese un número entre 0-5'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="fcvc"
										label="# de comidas principales al día donde suele comer verduras"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 5,
												message: 'Por favor, ingrese un número entre 0-5'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="ch2o"
										label="# de litros de agua que bebes al día"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 4,
												message: 'Por favor, ingrese un número entre 0-4'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="faf"
										label="# de días/semana que tiene actividad física"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 4,
												message: 'Por favor, ingrese un número entre 0-4'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="tue"
										label="# de horas/día de uso de dispositivos tecnológicos"
										rules={[
											{
												type: 'number',
												min: 0,
												max: 24,
												message: 'Por favor, ingrese un número entre 0-24'
											},
											{
												required: true,
												message: 'Por favor, ingrese un número'
											}
										]}>
										<InputNumber />
									</Form.Item>
									<Form.Item
										name="fhwo"
										label="¿Tienes familiares con sobrepeso?"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione'
											}
										]}>
										<Switch defaultChecked={false} />
									</Form.Item>
									<Form.Item
										name="favc"
										label="¿Consume frecuentemente alimentos altos en calorías?"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione'
											}
										]}>
										<Switch defaultChecked={false} />
									</Form.Item>
									<Form.Item
										name="caec"
										label="Frecuencia que ingiere comida entre horas"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione una opción'
											}
										]}>
										<Select placeholder="Selecciona una opción">
											<Option value="3">No</Option>
											<Option value="0">A veces</Option>
											<Option value="1">Frecuentemente</Option>
											<Option value="2">Siempre</Option>
										</Select>
									</Form.Item>
									<Form.Item
										name="smoke"
										label="¿Fuma frecuentemente?"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione'
											}
										]}>
										<Switch defaultChecked={false} />
									</Form.Item>
									<Form.Item
										name="scc"
										label="¿Monitorea las calorías que consume a diario?"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione'
											}
										]}>
										<Switch defaultChecked={false} />
									</Form.Item>
									<Form.Item
										name="calc"
										label="Frecuencia de ingesta de alcohol"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione una opción'
											}
										]}>
										<Select placeholder="Selecciona una opción">
											<Option value="0">No</Option>
											<Option value="1">A veces</Option>
											<Option value="2">Frecuentemente</Option>
											<Option value="3">Siempre</Option>
										</Select>
									</Form.Item>
									<Form.Item
										name="mtrans"
										label="Medio de transporte que usa habitualmente"
										rules={[
											{
												required: true,
												message: 'Por favor, seleccione una opción'
											}
										]}>
										<Select placeholder="Selecciona una opción">
											<Option value="2">Automóvil</Option>
											<Option value="3">Motocicleta</Option>
											<Option value="4">Bicicleta</Option>
											<Option value="0">Transporte público</Option>
											<Option value="1">Caminar</Option>
										</Select>
									</Form.Item>
									<Form.Item {...tailFormItemLayout}>
										<Button type="primary" htmlType="submit">
											Evaluar
										</Button>
									</Form.Item>
								</Form>
							</Col> {
								!consulting && status ?
									<Col md={12} sm={24} order={screens.xs ? 1 : 2}>
										<Row justify='center'>
											<Col span={24} style={{ display: 'flex', justifyContent: 'center' }}>
												<Title level={1}>Resultado</Title>
											</Col>
											<Col span={24} style={{ display: 'flex', justifyContent: 'center' }}>
												<img src={result.image} alt='bmi' style={{ width: '19%' }} />
											</Col>
											<Col span={24} style={{ display: 'flex', justifyContent: 'center' }}>
												<Title level={3}>{result.name}</Title>
											</Col>
										</Row>
									</Col> : null
							}

						</Row>
					</Layout>
				</Content>
				<Footer><Title level={4}>UNMSM - 2021</Title></Footer>
			</Spin>
		</Layout>
	)
}

export default App
